    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: fixed;
    z-index: 2400;
    width: 100vw;
    height: 10vh;
    // transition: transform 300ms ease;
    // background-color: rgba(255, 255, 255, 0.4);
    
    h1 {
        font-family: Virtuous;
        font-size: 20px;
        text-transform: lowercase;
        letter-spacing: 2px;
        display: flex;
        align-items: center;
        // height: 100%;
        // width: 45%;
        // padding-left: 5%;
        padding-top: 1vh;
        margin: 0;
        // background-color: rgba(255, 0, 0, 0.4);
    }

    ul {
        list-style: none;
        display: flex;
        flex-direction: row;
        // height: 100%;
        // width: 45%;
        // padding-top: 1vh;
        padding: 0;
        margin: 0;
        align-items: center;
        justify-content: center;
        // background-color: rgba(0, 255, 0, 0.4);

        li {
            font-size: 20px;
